import React, {Component} from "react";
import {integer, fraction} from "../../../../magnum2/components/Utils";
import CheckoutPrice from "./prices/CheckoutPrice"
import CheckoutPriceMobile from "./prices/CheckoutPriceMobile"
import MonthlyPrices from "./prices/MonthlyPrices"
import MonthlyPricesMobile from "./prices/MonthlyPricesMobile"

export default class ConvergentMultiCartCollapsedItemComponent extends Component {

    render() {
        return (
            <div className="l-full-row is-expanded-hide is-expanding-hide">
                <div className="l-page-row">
                    <div className="u-padding-top u-padding">
                        <div className="g-white1-bg u-box-shadow">
                            {this._renderDesktopView()}
                            {this._renderMobileView()}
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    _renderDesktopView() {
        return (
            <div className="u-small-hide u-padding-left-l u-padding-right-l">
                <table className="u-table-fixed">
                    <tbody>
                        <tr>
                            <td className="u-padding-l-xl u-padding-top-l-xl u-padding-right-l">
                                {this._renderIcons()}
                            </td>
                            <td className="u-padding-right l-col-2 u-small-hide u-padding-l u-padding-top-l u-text-right">
                                <CheckoutPrice checkoutPrice={this.props.entry.totalCheckoutPrice}/>
                            </td>
                            <td className="u-padding-left l-col-2 u-small-hide u-padding-l u-padding-top-l u-text-right">
                                <MonthlyPrices monthlyPrices={this.props.entry.totalMonthlyPrices} onlyFirst={true}/>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        );
    }

    _renderMobileView() {
        return (
            <div className="u-hide u-small-block u-padding-top-l">
                <div className="u-padding-left-l u-padding-right-l u-padding-l">
                    <table className="u-table-fixed">
                        <tbody>
                            <tr>
                                <td className="opl-checkout__image__cell u-vertical-middle">
                                    <span className="u-acc-hide">{this.props.entry.planName}</span>
                                    <img src="../../assets/images/articles/glorious-grid/orange-love.png" alt=""/>
                                </td>
                                <td>
                                    <MonthlyPricesMobile monthlyPrices={this.props.entry.totalMonthlyPrices}/>
                                    <CheckoutPriceMobile checkoutPrice={this.props.entry.totalCheckoutPrice}/>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div className="u-padding-top-l">
                        <a
                            href="#"
                            className={"opl-checkout__section__trigger " + this.props.trigger}
                            id="mod-core/modules/expander-3-control-0"
                            role="tab"
                            aria-controls="mod-core/modules/expander-3-tab-0"
                            aria-expanded="true"
                        >
                            Rozwi≈Ñ
                        </a>
                    </div>
                </div>
            </div>
        );
    }

    _renderIcons() {
        if (!this.props.entry.entries) {
            return null;
        }

        let icons = this.props.entry.entries.map((subEntry, index) => {
            const productSection = this.props.sectionConf.productSections.find(function(element) {
                return element.entryType === subEntry.subEntryType;
            });
            return this._renderIcon(subEntry, productSection, index!==this.props.entry.entries.length-1);
        });

        return (
            <div className="l-group">
                {icons}
            </div>
        );
    }

    _renderIcon(subEntry, productSection, visiblePlus) {
        return (
            <div key={subEntry.subEntryType + "_" + subEntry.productCode} style={{"float": "left"}}>
                <div className="l-group__element u-vertical-middle">
                    <span className="u-acc-hide">{subEntry.planName}</span>
                    <span aria-hidden="true" className={"g-icon g-icon--only g-icon--" + productSection.icon} />
                </div>
                {visiblePlus && this._renderPlusIcon()}
           </div>
        );
    }

    _renderPlusIcon() {
        return (
            <div className="l-group__element u-vertical-middle u-padding-left-l u-padding-right-l">
                <span className="g-icon g-icon--plus-slim g-icon--xs-s g-icon--only g-brand2-c" />
            </div>
        );
    }
}